import axios, { AxiosInstance } from "axios";
import ForecastResponse from "../interfaces/forecast-response";
import PointInfo from "../interfaces/point-info";
import StationObservationsResponse from "../interfaces/station-observations-response";
import StationsResponse from "../interfaces/stations-response";
import * as objectUtils from "../../common/utils/object.utils";
import { KeyofUnion } from "../../common/types/KeyofUnion";

const activeAlertParamsMap = new Map<KeyofUnion<GetActiveAlertsParams>, any>([
  ["messageType", "message_type"],
  ["regionType", "region_type"],
]);

class NWSService {
  constructor(private apiClient: AxiosInstance) {}

  async getActiveAlerts(params: GetActiveAlertsParams) {
    return await this.apiClient.get("/alerts/active", {
      params: objectUtils.remapKeys(params, activeAlertParamsMap),
    });
  }

  async getPointInfo(latitude: number, longitude: number): Promise<PointInfo> {
    return (await this.apiClient.get(`/points/${latitude},${longitude}`)).data;
  }

  async getForecast({
    wfo,
    x,
    y,
  }: GridPointsParams): Promise<ForecastResponse> {
    return (await this.apiClient.get(`/gridpoints/${wfo}/${x},${y}/forecast`))
      .data;
  }

  async getHourlyForecast({
    wfo,
    x,
    y,
  }: GridPointsParams): Promise<ForecastResponse> {
    return (
      await this.apiClient.get(`/gridpoints/${wfo}/${x},${y}/forecast/hourly`)
    ).data;
  }

  async getStations({
    wfo,
    x,
    y,
  }: GridPointsParams): Promise<StationsResponse> {
    return (await this.apiClient.get(`/gridpoints/${wfo}/${x},${y}/stations`))
      .data;
  }

  async getStationObservations({
    stationId,
    ...queryParams
  }: GetStationObservationsParams): Promise<StationObservationsResponse> {
    return (
      await this.apiClient.get(`/stations/${stationId}/observations`, {
        params: queryParams,
      })
    ).data;
  }
}

interface GetActiveAlertsBaseParams {
  status?: Array<"actual" | "exercise" | "system" | "test" | "draft">;
  messageType?: Array<"alert" | "update" | "cancel">;
  event?: string[];
  code?: string[];
  urgency?: Array<"unknown" | "past" | "future" | "expected" | "immediate">;
  severity?: Array<"unknown" | "minor" | "moderate" | "severe" | "extreme">;
  certainty?: Array<
    "unknown" | "unlikely" | "possible" | "likely" | "observed"
  >;
  limit?: number;
}

type GetActiveAlertsParams =
  | GetActiveAlertsBaseParams
  | GetActiveAlertsByRegionTypeParams
  | GetActiveAlertsByPointParams
  | GetActiveAlertsByRegion
  | GetActiveAlertsByArea
  | GetActiveAlertsByZone;

interface GetActiveAlertsByRegionTypeParams extends GetActiveAlertsBaseParams {
  regionType: "land" | "marine";
}

interface GetActiveAlertsByPointParams extends GetActiveAlertsBaseParams {
  x: number;
  y: number;
}

interface GetActiveAlertsByRegion extends GetActiveAlertsBaseParams {
  region: Array<"AL" | "AT" | "GL" | "GM" | "PA" | "PI">;
}

